___CSS_LOADER_EXPORT___.push([module.id, `.contents {
    display: grid;
    grid-row-gap: 16px;
}

@media screen and (max-width: 499px) {
    .header {
        display: flex;
        justify-content: center;
    }

    .header-menu {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .nav {
        z-index: 10;
        background-color: #fff;
        width: 300px;
        position: absolute;

        -webkit-transform: translate(-300px, 0);
        transform: translate(-300px, 0);

        transition: transform 0.3s ease;
    }

    .open {
        -webkit-transform: translate(0, 0);
        transform: translate(0, 0);
    }

    .nav-item {
        display: list-item;
        border-bottom: 1px solid #e0e0e0;
        width: 100%;
        text-align: left;
    }
}

@media screen and (min-width: 500px) {
    .header-menu {
        display: none;
    }
}

@media screen and (min-width: 650px) {
    .restaurant-title {
        font-size: 18px;
    }

    .restaurant-description {
        font-size: 14px;
    }
}

@media screen and (min-width: 700px) {
    .contents {
        display: grid;
    }

    #list-restaurant {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 10px;
        grid-row-gap: 16px;
    }
}

@media screen and (min-width: 800px) {
    .nav-list {
        max-width: 800px;
        margin: 0 auto;
    }
}

@media screen and (min-width: 1200px) {
    main {
        max-width: 1200px;
    }

    .hero img {
        min-width: 1000px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI;QACI,aAAa;QACb,uBAAuB;IAC3B;;IAEA;QACI,aAAa;QACb,mBAAmB;QACnB,uBAAuB;IAC3B;;IAEA;QACI,WAAW;QACX,sBAAsB;QACtB,YAAY;QACZ,kBAAkB;;QAElB,uCAAuC;QACvC,+BAA+B;;QAE/B,+BAA+B;IACnC;;IAEA;QACI,kCAAkC;QAClC,0BAA0B;IAC9B;;IAEA;QACI,kBAAkB;QAClB,gCAAgC;QAChC,WAAW;QACX,gBAAgB;IACpB;AACJ;;AAEA;IACI;QACI,aAAa;IACjB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,aAAa;IACjB;;IAEA;QACI,aAAa;QACb,8BAA8B;QAC9B,qBAAqB;QACrB,kBAAkB;IACtB;AACJ;;AAEA;IACI;QACI,gBAAgB;QAChB,cAAc;IAClB;AACJ;;AAEA;IACI;QACI,iBAAiB;IACrB;;IAEA;QACI,iBAAiB;IACrB;AACJ","sourcesContent":[".contents {\r\n    display: grid;\r\n    grid-row-gap: 16px;\r\n}\r\n\r\n@media screen and (max-width: 499px) {\r\n    .header {\r\n        display: flex;\r\n        justify-content: center;\r\n    }\r\n\r\n    .header-menu {\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n    }\r\n\r\n    .nav {\r\n        z-index: 10;\r\n        background-color: #fff;\r\n        width: 300px;\r\n        position: absolute;\r\n\r\n        -webkit-transform: translate(-300px, 0);\r\n        transform: translate(-300px, 0);\r\n\r\n        transition: transform 0.3s ease;\r\n    }\r\n\r\n    .open {\r\n        -webkit-transform: translate(0, 0);\r\n        transform: translate(0, 0);\r\n    }\r\n\r\n    .nav-item {\r\n        display: list-item;\r\n        border-bottom: 1px solid #e0e0e0;\r\n        width: 100%;\r\n        text-align: left;\r\n    }\r\n}\r\n\r\n@media screen and (min-width: 500px) {\r\n    .header-menu {\r\n        display: none;\r\n    }\r\n}\r\n\r\n@media screen and (min-width: 650px) {\r\n    .restaurant-title {\r\n        font-size: 18px;\r\n    }\r\n\r\n    .restaurant-description {\r\n        font-size: 14px;\r\n    }\r\n}\r\n\r\n@media screen and (min-width: 700px) {\r\n    .contents {\r\n        display: grid